
@mixin coverer {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
 }
 html,body{
     background-color: black;
     color:ivory;
     height: auto;
     margin: 0;
     overflow-x: hidden;
     padding: 0;
     text-align: center;
     width: 100vw;
 }
  main{
    display: flex;
    flex-direction: column;
    align-items: center;
   /*  position: relative;
    margin-top: 105vh;
    &::before {
      content: "";
      @include coverer;
      top: -90vh;
    } */
  }
h1 {color:$gold;
    margin-top:1rem;}
 a{
     text-decoration: none;
     color: $gold;
 }
 ul{
   list-style: none;
 }
 .continente__box{
padding-top: 6rem;
 }
 .contacto__page{
   margin-top: 6rem;
 }
 .custom__button{
  background-color:rgba($color: $gold, $alpha: 0.8);
  border: 1px solid $gold;
  border-radius: 5px;
  color: black;
  font-size: 1.2rem;
  font-weight: bolder;
  padding: 0.5rem 1.5rem;
  text-shadow: 1px 1px 1px ivory;
}

header{
  align-items: center;
  background-image: url("../../images/portada.png");
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 95vh;
  justify-content: center;
  margin-bottom: 2rem;
  & h1{
    text-shadow: 1px 1px 1px black;
    position: absolute;
    top :75%
  }

}

@media (min-width: 750px) {
  body{
    font-size: 1.2rem;
  }

  header{
    height: 100vh;
    & h1{
      visibility: hidden;
    }
  }
  .contacto__page{
    margin-top: 8rem;
  }
  .continente__box{
    padding-top: 14rem;
     }
  .socialmedia{visibility: hidden;}
}

@media (min-width: 1000px) {
 
  .contacto__page{
    margin-top: 12rem;
  }
  .continente__box{
    padding-top: 15rem;
     }
}